@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: #3f3f3f;
}



.card{
    border-radius: 10px;
    box-shadow: 0 1px 8px rgb(0,0,0.025);
}
.whole-date{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 2px solid hsl(317 100% 54%);
    background-color: rgb(44, 42, 42);
    color: white;
    height: 6rem;
    width: 6.5rem;
    border-radius: 10px;
}
.month{
    font-weight: 600;
}
.day{
    font-size: 1.4rem;
    font-weight: 700;
}
.outer-div{
    margin: 0.8rem auto;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color:#555555 ;
    padding: 0.65rem 0;
    width: 97%;
}
.description-div{
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
    padding: 0 0.5rem;
}
.price{
    font-size: 1rem;
    background-color: rgb(88, 5, 88);
    border: 1px solid rgb(245, 242, 242);
    border-radius: 10px;
    padding: 0.5em 1em;
}

.allExpenses{
    margin:  auto;
    max-width: 60%;
    padding: 0.75rem 0;

}
p{
    font-size: 1.5rem;
    color: white;
    text-align: center;
}
.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: #f8dfff;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
  }
  
.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .chart-bar__inner {
    height: 100%;
    width: 100%;
    border: 1px solid #313131;
    border-radius: 12px;
    background-color: #c3b4f3;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .chart-bar__fill {
    background-color: #4826b9;
    width: 100%;
    transition: all 0.3s ease-out;
  }
  
  .chart-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
  }
.expenses-filter {
    color: white;
    padding: 0 1rem;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }
.new-expense {
    background-color: #a892ee;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
    position: relative;
  }
  
  .new-expense button {
      position: absolute;
      right: 1em;
      bottom: 1em;
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
    margin-right: 1rem;
  }
  
  .new-expense button:hover,
  .new-expense button:active {
    background-color: #510674;
    border-color: #510674;
  }
  
  .new-expense button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
  }
  
  .new-expense button.alternative:hover,
  .new-expense button.alternative:active {
    background-color: #ddb3f8;
  }
.new-expense-outer {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-expense-inner label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense-inner input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
    outline: none;
  }
  
  
  
